/* eslint-disable */
// 封装球形firework
import { mat4 } from "gl-matrix";
import util from "@/util/index";
// 初始化webgl的point数据和color数据
Ball.prototype.initData = function () {
    const fireData = [];
    const colorMap = {
        '紫红': [204, 50, 153],  //锂离子焰色反应
        '黄绿': [147, 219, 112], //钡离子焰色反应
        '砖红': [142, 35, 35],   //钙离子焰色反应
        '紫色': [153, 50, 205],  //铷离子焰色反应
        '绿色': [0, 255, 0],     //铜离子焰色反应
        '黄色': [255, 255, 0],   //钠离子焰色反应
    }
    const colorArray = [
        colorMap['黄色'],
        colorMap['黄绿'],
        colorMap['砖红'],
    ];
    for (let i = 0; i < 300;) {
        let v = Math.random() * 2 - 1;
        let u = Math.random() * 2 - 1;
        let r2 = v * v + u * u;
        let x = 2 * u * Math.sqrt(1 - r2);
        let y = 2 * v * Math.sqrt(1 - r2);
        let z = 1 - 2 * r2;
        if (r2 < 1) {
            fireData.push(x, y, z);
            i++;
            fireData.push(
                colorArray[i % 3][0] / 255,
                colorArray[i % 3][1] / 255,
                colorArray[i % 3][2] / 255
            );
        }
    }
    return fireData
}
const vertexShaderSource = `
attribute vec4 vertexPoint;
attribute vec4 vertexColor;
uniform mat4 modelViewMatrix;
uniform mat4 colorMatrix;
uniform mat4 projectionMatrix;

varying lowp vec4 vColor; 

void main(){
    gl_Position = projectionMatrix * modelViewMatrix * vertexPoint;
    gl_PointSize = 1.2;
    vColor = colorMatrix * vertexColor;
}
`;
const fragShanderSource = `

varying lowp vec4 vColor;

void main(){
    gl_FragColor = vColor;
}
`
function Ball(gl, x, y) {
    this.data = {
        step: -4,
        x, y, done: false,
        calScale: []
    }

    if (Ball.programInfo) {
        return
    }
    this.fireData = this.initData()

    const shaderProgram = util.initShaderProgram(
        gl,
        vertexShaderSource,
        fragShanderSource
    );
    const buffer = gl.createBuffer();
    gl.bindBuffer(gl.ARRAY_BUFFER, buffer);
    gl.bufferData(
        gl.ARRAY_BUFFER,
        new Float32Array(this.fireData),
        gl.STATIC_DRAW
    );
    Ball.programInfo = {
        gl,
        shaderProgram,
        attribLocations: {
            vertexPoint: gl.getAttribLocation(shaderProgram, "vertexPoint"),
            vertexColor: gl.getAttribLocation(shaderProgram, "vertexColor"),
        },
        uniformLocations: {
            modelViewMatrix: gl.getUniformLocation(
                shaderProgram,
                "modelViewMatrix"
            ),
            projectionMatrix: gl.getUniformLocation(
                shaderProgram,
                "projectionMatrix"
            ),
            colorMatrix: gl.getUniformLocation(
                shaderProgram,
                "colorMatrix"
            ),
            //colorMatrix: gl.getUniformLocation(shaderProgram, "colorMatrix"),
        },
        buffer
    };
    {
        const fieldOfView = (45 * Math.PI) / 180; // in radians
        const aspect = gl.canvas.clientWidth / gl.canvas.clientHeight;
        const zNear = 0.1;
        const zFar = 100.0;
        const projectionMatrix = mat4.create();

        // note: glmatrix.js always has the first argument
        // as the destination to receive the result.
        mat4.perspective(projectionMatrix, fieldOfView, aspect, zNear, zFar);
        // Set the drawing position to the "identity" point, which is
        // the center of the scene.
        gl.uniformMatrix4fv(
            Ball.programInfo.uniformLocations.projectionMatrix,
            false,
            projectionMatrix
        );
    }
}



Ball.prototype.step = function () {
    if (this.data.done) return
    this.data.step > 1 ? this.data.step += 0.01 : this.data.step += 0.03;
    if (this.data.step <= 0) {
        return
    }
    this.data.calScale.unshift(this.calBystep(this.data.step))
    if (this.data.calScale[0].scale > 1.3) {
        this.data.calScale = []
        this.data.step = -4
        return
    }
    const gl = Ball.programInfo.gl
    gl.useProgram(Ball.programInfo.shaderProgram);
    gl.bindBuffer(gl.ARRAY_BUFFER, Ball.programInfo.buffer);
    {
        const numComponents = 3; // pull out 3 values per iteration
        const type = gl.FLOAT; // the data in the buffer is 32bit floats
        const normalize = false; // don't normalize
        const stride = 24; // how many bytes to get from one set of values to the next
        // 0 = use type and numComponents above
        const offset = 0; // how many bytes inside the buffer to start from
        //gl.bindBuffer(gl.ARRAY_BUFFER, Ball.programInfo.buffers);
        gl.vertexAttribPointer(
            Ball.programInfo.attribLocations.vertexPoint,
            numComponents,
            type,
            normalize,
            stride,
            offset
        );
        gl.enableVertexAttribArray(Ball.programInfo.attribLocations.vertexPoint);
    }
    {
        const numComponents = 3; // pull out 3 values per iteration
        const type = gl.FLOAT; // the data in the buffer is 32bit floats
        const normalize = false; // don't normalize
        const stride = 24; // how many bytes to get from one set of values to the next
        // 0 = use type and numComponents above
        const offset = 12; // how many bytes inside the buffer to start from
        //gl.bindBuffer(gl.ARRAY_BUFFER, Ball.programInfo.buffers);
        gl.vertexAttribPointer(
            Ball.programInfo.attribLocations.vertexColor,
            numComponents,
            type,
            normalize,
            stride,
            offset
        );
        gl.enableVertexAttribArray(Ball.programInfo.attribLocations.vertexColor);
    }
    for (let i = 0; i < Math.min(this.data.calScale.length, 7); i++) {
        gl.uniformMatrix4fv(
            Ball.programInfo.uniformLocations.modelViewMatrix,
            false,
            this.data.calScale[i].v
        );
        gl.uniformMatrix4fv(
            Ball.programInfo.uniformLocations.colorMatrix,
            false,
            this.data.calScale[i].v1
        );
        const offset = 0;
        gl.drawArrays(gl.POINTS, offset, 300);
    }
}

const modelViewMatrix = mat4.create();
const colorMatrix = mat4.create();
Ball.prototype.calBystep = function (step) {
    let scale = Math.pow(step, 1 / 3);
    let colorScale = step > 1 ? Math.pow(2 - scale, 10) : step
    let v = [], v1 = [];



    //const colorMatrix = mat4.create();
    mat4.rotateZ(
        v, // destination matrix
        modelViewMatrix, // matrix to translate
        Math.PI / 2
    );
    // Now move the drawing position a bit to where we want to
    // start drawing the square.
    mat4.translate(
        v, // destination matrix
        v, // matrix to translate
        [this.data.x, this.data.y, -20.0]
    );
    mat4.scale(
        v, // destination matrix
        v, // matrix to translate
        [scale, scale, 1]
    ); // amount to translate
    //this.data.step - 1
    mat4.scale(
        v1, // destination matrix
        colorMatrix, // destination matrix
        [colorScale, colorScale, colorScale]
    ); // amount to translate
    return { v, v1, scale }
}

Ball.programInfo = null

export default Ball