/* eslint-disable */
// 封装文字firework
import { mat4 } from "gl-matrix";
import util from "@/util/index";
// 初始化webgl的point数据和color数据
Word.prototype.initData = function () {
    const fireData = [];
    // 定义中心点
    fireData.push(0.0, 0.0, -20.0)
    for (let i = 0; i <= 360; i += 5) {
        fireData.push(Math.cos(i * Math.PI / 180) * 0.03, Math.sin(i * Math.PI / 180) * 0.03, -20.0)
    }
    return fireData
}
const vertexShaderSource = `
attribute vec4 vertexPoint;
uniform mat4 modelViewMatrix;
uniform mat4 projectionMatrix;
uniform mat4 colorMatrix;
varying lowp vec4 vColor; 
void main(){
    gl_Position = projectionMatrix * modelViewMatrix * vertexPoint;
    vColor = colorMatrix * vec4(0.5568,0.1372,0.1372,1.0);
}
`;
const fragShanderSource = `
varying lowp vec4 vColor; 
void main(){
    gl_FragColor = vColor;
}
`
function Word(gl, translateData, x, y) {
    this.data = {
        // 处理缩放的原始数据
        step: -4,
        // 点位左边和是否完成标志
        x, y, done: false,
        // 缩放矩阵对象数组
        calScale: [],
        // 点位数量
        pointsNum: 160,
        // 各个点的偏移量
        translateData
    }

    if (Word.programInfo) {
        return
    }
    this.fireData = this.initData()

    const shaderProgram = util.initShaderProgram(
        gl,
        vertexShaderSource,
        fragShanderSource
    );

    const buffer = gl.createBuffer();
    gl.bindBuffer(gl.ARRAY_BUFFER, buffer);
    gl.bufferData(
        gl.ARRAY_BUFFER,
        new Float32Array(this.fireData),
        gl.STATIC_DRAW
    );

    Word.programInfo = {
        gl,
        shaderProgram,
        attribLocations: {
            vertexPoint: gl.getAttribLocation(shaderProgram, "vertexPoint")
        },
        uniformLocations: {
            modelViewMatrix: gl.getUniformLocation(
                shaderProgram,
                "modelViewMatrix"
            ),
            projectionMatrix: gl.getUniformLocation(
                shaderProgram,
                "projectionMatrix"
            ),
            colorMatrix: gl.getUniformLocation(shaderProgram, "colorMatrix"),
        },
        buffer
    };
    //gl.useProgram(shaderProgram);
    {
        const fieldOfView = (45 * Math.PI) / 180; // in radians
        const aspect = gl.canvas.clientWidth / gl.canvas.clientHeight;
        const zNear = 0.1;
        const zFar = 100.0;
        const projectionMatrix = mat4.create();

        // note: glmatrix.js always has the first argument
        // as the destination to receive the result.
        mat4.perspective(projectionMatrix, fieldOfView, aspect, zNear, zFar);
        // Set the drawing position to the "identity" point, which is
        // the center of the scene.
        gl.uniformMatrix4fv(
            Word.programInfo.uniformLocations.projectionMatrix,
            false,
            projectionMatrix
        );
    }
}


Word.prototype.step = function () {
    if (this.data.done) return
    this.data.step > 1 ? this.data.step += 0.01 : this.data.step += 0.03;
    if (this.data.step <= 0) {
        return
    }
    const calScale = this.calBystep(this.data.step)

    if (calScale.scale > 1.3) {
        this.data.step = -4
        return
    }
    const gl = Word.programInfo.gl
    gl.useProgram(Word.programInfo.shaderProgram);
    gl.bindBuffer(gl.ARRAY_BUFFER, Word.programInfo.buffer);
    {
        const numComponents = 3; // pull out 3 values per iteration
        const type = gl.FLOAT; // the data in the buffer is 32bit floats
        const normalize = false; // don't normalize
        const stride = 0; // how many bytes to get from one set of values to the next
        // 0 = use type and numComponents above
        const offset = 0; // how many bytes inside the buffer to start from
        //gl.bindBuffer(gl.ARRAY_BUFFER, Word.programInfo.buffers);
        gl.vertexAttribPointer(
            Word.programInfo.attribLocations.vertexPoint,
            numComponents,
            type,
            normalize,
            stride,
            offset
        );
        gl.enableVertexAttribArray(Word.programInfo.attribLocations.vertexPoint);
    }
    gl.uniformMatrix4fv(
        Word.programInfo.uniformLocations.colorMatrix,
        false,
        calScale.v1
    );
    let offset = 0;
    for (let i = 0; i < this.data.translateData.length; i++) {
        gl.uniformMatrix4fv(
            Word.programInfo.uniformLocations.modelViewMatrix,
            false,
            calScale.v[i]
        );
        gl.drawArrays(gl.TRIANGLE_FAN, offset, 74);
    }
}

const modelViewMatrix = mat4.create();
const colorMatrix = mat4.create();
Word.prototype.calBystep = function (step) {
    let scale = Math.pow(step, 1 / 3);
    let colorScale = step > 1 ? Math.pow(2 - scale, 10) : scale
    let v = [], v1 = [];
    for (let i = 0; i < this.data.translateData.length; i++) {
        let v_inner = []
        /*mat4.rotateZ(
            v_inner, // destination matrix
            modelViewMatrix, // matrix to translate
            -Math.PI / 2
        );*/

        /*mat4.translate(
            v_inner, // destination matrix
            v_inner, // matrix to translate
            [0, Math.min(scale, 1) * 15 - 15, 0]
        );*/
        mat4.translate(
            v_inner, // destination matrix
            modelViewMatrix, // matrix to translate
            [this.data.translateData[i][0] * scale + this.data.x, this.data.translateData[i][1] * scale + this.data.y, this.data.translateData[i][2] * scale]
        );
        v.push(v_inner)
    }
    //const colorMatrix = mat4.create();
    /*mat4.rotateZ(
        v, // destination matrix
        modelViewMatrix, // matrix to translate
        Math.PI / 2
    );*/
    // Now move the drawing position a bit to where we want to
    // start drawing the square.
    // amount to translate
    //this.data.step - 1
    mat4.scale(
        v1, // destination matrix
        colorMatrix, // destination matrix
        [colorScale, colorScale, colorScale]
    ); // amount to translate
    return { v, v1, scale }
}

Word.programInfo = null
export default Word