<template>
  <div id="fire">
    <canvas id="canvas-rect"></canvas>
  </div>
</template>

<script>
/* eslint-disable */
import NoSleep from "nosleep.js";
import Ball from "@/components/firework/Ball";
import Word from "@/components/firework/Word";
import util from "@/util/index";
import word1 from "@/components/firework/Word1.js";
export default {
  data() {
    return {};
  },
  mounted() {
    //init noSleep
    const noSleep = new NoSleep();
    // Enable wake lock.
    // (must be wrapped in a user input event handler e.g. a mouse or touch handler)
    //  noSleep.enable()初始化方法必须绑定在鼠标事件或触摸事件内，
    //  当点击页面时就会激活不休眠功能
    document.addEventListener(
      "click",
      function enableNoSleep() {
        document.removeEventListener("click", enableNoSleep, false);
        noSleep.enable();
        console.log("enable noSleep");
      },
      false
    );
    const canvas = document.getElementById("canvas-rect");
    canvas.width = document.body.clientWidth;
    canvas.height = document.body.clientHeight;
    const gl = util.getGl("canvas-rect");
    if (!gl) return;

    const fireWorkList = [
      //new Word(gl, 0, 3),
      new Ball(gl, -3, 3),
      //new Ball(gl, -2, 3),

      new Ball(gl, -1, 3),
      //new Ball(gl, 0, 3),
      new Ball(gl, 1, 3),

      //new Ball(gl, 2, 3),
      new Ball(gl, 3, 3),
      //new Ball(gl, 4, 3),

      new Ball(gl, 5, 3),
      new Word(gl, word1["生"], 3, 3),
      new Word(gl, word1["日"], 5, 3),
      //new Word(gl, 0, 0),
      //new Word(gl, 2, 0),
      //new Word(gl, 4, 0),
      //new Ball(gl, 6, 3),
    ];
    //new Ball(gl, -3, 3)
    //const wordList = [new Word(gl, 3, 3), new Word(gl, 0, 3)];
    //const ball1 = new Ball(gl);
    function render() {
      // 清空背景
      gl.clearColor(0.0, 0.0, 0.0, 1.0); // Clear to black, fully opaque
      gl.clearDepth(1.0); // Clear everything
      gl.enable(gl.DEPTH_TEST); // Enable depth testing
      gl.depthFunc(gl.LEQUAL); // Near things obscure far things
      gl.clear(gl.COLOR_BUFFER_BIT | gl.DEPTH_BUFFER_BIT);

      for (let i = 0; i < fireWorkList.length; i++) {
        fireWorkList[i].step();
      }
      //ball1.start();
      requestAnimationFrame(render);
    }
    render();
  },
};
</script>

<style  scoped>
#fire {
  width: 100vw;
  height: 100vh;
}
</style>
